.toast-alert {
	position: fixed;
	display: none;
	bottom: 50px;
	left: 50%;
	min-width: 200px;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	z-index: 100;
}

.sticky {
	position: sticky !important;
	float: left;
	height: auto;
	top: 2rem;
}

.nabvar-item,
.nabvar-item:hover,
.nabvar-item:active,
.nabvar-item:visited {
	color: inherit;
	cursor: pointer;
}

.multiselect-form {
	display: inline-block;
	vertical-align: top;
	width: 100%;
}
.tags {
	display: inline-block;
	vertical-align: top;
	max-width: 200px;
	height: auto;
}
.input {
	display: inline-block;
	vertical-align: top;
	border: 0;
	outline: 0;
	color: #495057;
	font-family: inherit;
}
.options {
	position: absolute;
	top: 100%; left: -1px;
	width: calc(100% + 2px);
	background: white;
	border: 1px solid #becad6;
	border-bottom-left-radius: .375rem;
	border-bottom-right-radius: .375rem;
	margin-top: -0.375rem;
	overflow: hidden;
	z-index: 10;
}
.options .option {
	display: inline-block;
	vertical-align: top;
	width: 100%;
	height: auto;
	padding: 5px 10px;
	color: #495057;
	border-bottom: 1px solid #becad6;
	-webkit-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out;
}
.options .option:last-child { border: none;
}
.options .option:hover, .options .option:active { 
	background: #007bff; 
	color: white;
	cursor: pointer;
}

.dragging {
	opacity: 0.4;
}


























































































































































































